ul.uw-sidebar-menu,
ul.uw-mobile-menu {
	margin-left: 0;
	margin-right: 0;
	padding: 0 0 20px 0;
	// margin-top: -80px;
	background-color: white;

	ul,
	li {
		display: block;
		list-style: none;
		padding: 0;
		margin: 0;
		-webkit-font-smoothing: auto;
	}

	li.pagenav,
	>div {

		a,
		span {
			// font-family: @font-family-headline;
			display: block;
			font-size: 16px;
			padding: 10px 10px 10px 20px;

			&.homelink {
				display: none;
				color: $purple;
				font-size: 25px;
			}
		}

		ul {

			line-height: 1.3;
			font-size: 19px;

			li.current_page_item,
			li.current_page_parent {
				background-color: $purple;

				&>a,
				&>span {
					font-weight: bold;
					padding-left: 19px;
					padding-top: 16px;
					padding-bottom: 16px;
				}

				a,
				span {
					color: white;
					border-bottom: none;
					border-left: 10px solid #b7a57a;
				}

				&.current_page_ancestor {
					position: relative;

					&>a {
						padding-right: 60px;

						&:hover,
						&:focus {
							color: $gold;
						}

						&:after {
							content: "";
							background: url(../assets/svg/uw-sprite.svg) no-repeat -191px -478px;
							display: inline-block;
							height: 52px;
							width: 44px;
							margin-left: 7px;
							margin-top: -26px;
							position: absolute;
							right: 0;
							top: 50%;
							background-color: #b7a57a;
						}
					}

					a,
					span {
						border-left: none;
						position: relative;

					}
				}

				li.page_item_has_children ul {
					display: none;
				}

				li.page_item_has_children.current_page_item ul {
					display: block;
				}

			}

			li {
				a {
					color: $purple;
					border-bottom: thin solid #eee;
				}

				&.current_page_item ul.children,
				&.current_page_ancestor ul.children {
					display: block;
					font-size: 17px;
					background-color: white;

					li {
						background-color: #eee;
						border-bottom: 1px solid #fff;

						&.active {
							a {
								border-left: 10px solid #b7a57a;
							}
						}

						a,
						span {
							color: $darkgray;
							padding: 10px 30px 10px 30px;
							border-left: none;

							&:hover,
							&:focus {
								background-color: #DDD;
							}

						}

					}

					li.current_page_item {

						// background-color:white;
						ul.children {
							li {
								border-bottom: 1px solid #E0E0E0;
							}

							li:last-child {
								border-bottom: none;
							}

							a {
								background-color: white;
								color: $darkgray;
								border-left: none;
								padding-left: 25px;

								&:before {
									content: "- ";
								}
							}
						}

						span {
							// background-color:#757575;
							border-left: 10px solid #b7a57a;
							padding-left: 20px;
							background-color: #ddd;

							a {
								color: white;
								border-bottom: none;
							}

							// color:white;
							border-bottom: none;
						}
					}


				}


			}

			li:last-child {
				a {
					// color: $purple;
					border-bottom: none;
				}
			}

		}

	}

	ul.children {
		display: none;
	}

}

// Sidebar widget titles

.uw-sidebar {
	.widgettitle {
		clear: left;
		font-weight: $ultra;
		font-size: 22px;
		text-transform: uppercase;
		position: relative;
		padding-bottom: 20px;

		&:after,
		&:before {
			position: absolute;
			left: 0;
			bottom: 5px;
			content: "";
			height: 4px;
		}

		&:before {
			width: 100px;
			background-color: $gold;
		}

		&:after {
			width: 40px;
			@include skew(-25deg, 0);
			left: 80px;
			background-color: white;
			bottom: 3px;
			height: 8px;
		}
	}
}


body.home .uw-sidebar-menu {
	//	display: none;
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
	ul.uw-sidebar-menu {
		padding: 0;
		margin: 50px 0 0 0;
		clear: both;
	}

}

@media only screen and (min-width : 768px) {
	.uw-sidebar {
		padding-top: 40px;
	}

	.page-template-no-title {

		.uw-sidebar-menu,
		.uw-mobile-menu {
			margin-top: 0;
		}
	}
}

.hide {
	display: none !important;
}
