 [class^="ic-"], [class*=" ic-"] {

	  &:before {
		  font-size: 70px;
		  color: $purple;
		  display: block;
		  font-family: 'uw-icon';
		  speak: none;
		  font-style: normal;
		  font-weight: normal;
		  font-variant: normal;
		  text-transform: none;
		  line-height: 1;
		  /* Better Font Rendering =========== */
		  -webkit-font-smoothing: antialiased;
		  -moz-osx-font-smoothing: grayscale;

	  }
	  &.gold {
		  &:before,
		  a {
			  color: $darkgold;
		  }
	  }
	  &.gray {
		  &:before,
		  a {
			  color: $darkgray;
		  }
	  }
  }
  .sm  {
	  &:before {
		  font-size: 35px;
	  }
  }
  .lg  {
	  &:before {
		  font-size: 100px;
	  }
  }
