  // Menu using icons

  .icon-menu {
	  text-align: center;
	  margin: 40px 0;
	  border-top: 1px solid #ddd;
	  border-bottom: 1px solid #ddd;
	  ul {
		  padding-left: 0;
	  }
	  li {
		  list-style: none;
		  text-align: center;
		  display: inline-block;
		  padding: 30px;
		  position: relative;
  //		&:after {
  //    		content: "";
  //    		display: block;
  //    		width: 1px;
  //    		height: 100px;
  //    		background-color: #ddd;
  //    		position: absolute;
  //    		right: -10px;
  //    		top: 50%;
  //    		margin-top: -50px;
  //    		@include skew(-15deg, 0);
  //		}
	  }
	  a {
		  line-height: $headings-line-height;
		  font-family: $headings;
		  font-size: 16px;
		  font-weight: 900;
		  color: $purple;
		  display: inline-block;
		  padding-top: 100px;
		  margin-top: -85px;
		  text-decoration: none;
		  @include transition(all .5s cubic-bezier(0.165, 0.84, 0.44, 1));
		  &:hover,
		  &:focus {
			  transform: translate(0, -10px);
			  color: $darkgold;
		  }
	  }
  }


  @media only screen and (max-width : 767px) {

	  .icon-menu {
		  ul {

		  }
		  li {
			  padding: 20px 0px;
			  width: 47%;
		  }
		  a {

		  }
	  }

  }


  .icon-list {
	  list-style: none;
	  margin: 0;

	  li {
		  float: left;
		  display: block;
		  width: 20%;
		  text-align: center;
	  }
  }



  @media screen and (max-width: 991px) {

	  .icon-list li {
		  width: 100%;
	  }

  }


